.formSearch {
  padding: .5rem 0;
  display: flex;
  justify-content: center;
}

.inputSearch {
  width: 14rem;
  height: 1.2rem;
  margin: 0 .4rem 0 0;
  border-radius: .2rem;
  border: none;
  outline: none;
  padding: 0 .2rem;
}

.inputSearch:focus {
  box-shadow: 0 0 .4rem .1rem rgba( 255, 255, 255, .4 );
}

.btn {
  height: 1.2rem;
  border-radius: .2rem;
  border: none;
  outline: none;
  background: #BDB16E;
  padding: 0 .4rem;
}

.btn:hover, .btn:focus {
  background: #D1C47A;
}